  min-width: 100%;
  padding: 3rem 2rem;
  background: linear-gradient(180deg, rgba(124, 58, 237, 0.14), rgba(86, 182, 194, 0.12));
}

.slide h3 {
  margin: 0 0 0.5rem;
}

.slide p {
  margin: 0;
  color: #94a3b8;
}

.carousel-control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.35);
  border: 0;
  color: white;
  width: 44px;
  height: 44px;
  border-radius: 999px;
  display: grid;
  place-items: center;
  cursor: pointer;
}

.carousel-control.prev {
  left: 0.75rem;
}

.carousel-control.next {
  right: 0.75rem;
}

.parallax {
  background-image: url("https://images.unsplash.com/photo-1518779578993-ec3579fee39f?q=80&w=2000&auto=format&fit=crop");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  position: relative;
}

.parallax .parallax-inner {
  background: rgba(0, 0, 0, 0.35);
  min-height: 60vh;
}

.parallax .overlay {
  font-size: clamp(1.75rem, 3vw + 1rem, 3rem);
}

.cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

.card {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.04), rgba(255, 255, 255, 0.02));
  border: 1px solid rgba(255, 255, 255, 0.08);
  padding: 1rem;
  border-radius: 16px;
}

.card h3 {
  margin-top: 0;
}

.card p {
  color: #94a3b8;
}

.video video {
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.08);
}

.social {
  display: flex;
  gap: 0.75rem;
  list-style: none;
  padding: 0;
}

.social a {
  display: inline-grid;
  place-items: center;
  width: 44px;
  height: 44px;
  border-radius: 999px;
  background: rgba(255, 255, 255, 0.06);
  color: #e9eef5;
  text-decoration: none;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.footer {
  border-top: 1px solid rgba(255, 255, 255, 0.08);
}

.footer .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.back-to-top {
  color: #e9eef5;
  text-decoration: none;
  background: rgba(255, 255, 255, 0.06);
  padding: 0.5rem;
  border-radius: 0.5rem;
}

.modal {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.55);
  display: none;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.modal[aria-hidden=false] {
  display: flex;
}

.modal-dialog {
  width: min(560px, 96vw);
  background: #0e141b;
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  padding: 1.25rem 1.25rem 1rem;
  position: relative;
}

.modal-close {
  position: absolute;
  right: 0.5rem;
  top: 0.5rem;
  background: none;
  border: 0;
  color: #e9eef5;
  font-size: 1.25rem;
  cursor: pointer;
}

@media (max-width: 1280px) {
  .about .columns {
    -moz-column-count: 2;
         column-count: 2;
  }
}
@media (max-width: 1024px) {
  .cards {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 768px) {
  .navbar {
    height: 56px;
  }

  .nav-toggle {
    display: inline-grid;
  }

  .nav-links {
    position: absolute;
    right: 1rem;
    top: calc(100% + .5rem);
    background: #0e141b;
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 0.75rem;
    padding: 0.5rem;
    display: none;
    flex-direction: column;
  }

  .nav-links.open {
    display: flex;
  }

  .about .columns {
    -moz-column-count: 1;
         column-count: 1;
  }

  .cards {
    grid-template-columns: 1fr;
  }
}
:focus {
  outline: 3px solid #56b6c2;
  outline-offset: 2px;
